<template>
  <div id="loginPage">
    <form @submit.prevent="userLogin" class="loginForm p-2" ref="loginForm">
      <h2 class="title">FmaxCloud</h2>
      <span class="subtitle">欢迎登录Fmaxcloud官网</span>
      <vs-input
        v-model.lazy="form.username"
        primary
        placeholder="请输入您的用户名"
        class="mt-3"
      >
        <template #icon>
          <i class="bx bx-user"></i>
        </template>
        <template v-if="!form.username" #message-danger> 必填项 </template>
      </vs-input>
      <vs-input
        v-model.lazy="form.password"
        type="password"
        :visiblePassword="hasVisiblePassword"
        @click-icon="hasVisiblePassword = !hasVisiblePassword"
        icon-after
        danger
        placeholder="请输入您的密码"
        class="mt-3"
      >
        <template #icon>
          <!-- <i class="bx bx-lock-open-alt"></i> -->
          <i v-if="!hasVisiblePassword" class="bx bx-show-alt"></i>
          <i v-else class="bx bx-hide"></i>
        </template>
        <template v-if="!form.password" #message-danger> 必填项 </template>
      </vs-input>

      <!-- 人机验证模块 -->
      <Vaptcha
        :passCallback="vpass"
        ref="vaptchaVc"
        style="line-height: normal"
        class="mt-3"
      />
      <vs-checkbox v-model="form.NologinType" class="mt-3">
        7天内免登录
      </vs-checkbox>

      <vs-button @click="UserLogin" size="large" class="mt-3"> 登录 </vs-button>
      <hr />
      <button v-wave class="btn btn-info btn-relief" @click="quickLogin('qq')">
        使用QQ登录快速登录/注册
      </button>
      <button
        v-wave
        class="btn btn-success btn-relief mt-3"
        @click="quickLogin('wx')"
      >
        使用微信快速登录/注册
      </button>
      <span class="regspan"
        >没有账号？ <router-link to="/auth/register">前往注册</router-link> 或
        <router-link to="/auth/forgot">忘记密码？</router-link></span
      >
      <span class="retrieve"
        >如果您找不回账号,可以<a href="#">咨询客服</a>协助找回</span
      >
    </form>
  </div>
</template>

<script>
import { login } from "../../scripts/auth/";
import _ from "lodash";
import Vaptcha from "../../components/Vaptcha.vue";

export default {
  name: "Login",
  data() {
    return {
      form: {
        username: "",
        password: "",
        vaptcha: {
          server: "",
          token: "",
        },
      },
      hasVisiblePassword: false,
    };
  },
  components: {
    Vaptcha,
  },
  async mounted() {
    if ((await this.$store.state.userData.pullData(this, false, true)) === 1)
      this.$router.replace({ name: "rDashboardMain" });
  },
  methods: {
    quickLogin() {
      // this.$toastx({
      //     title: '暂不支持',
      //     type: 'warning'
      // })
    },
    vpass(data) {
      this.form.vaptcha.server = data.server;
      this.form.vaptcha.token = data.token;
    },
    UserLogin() {
      if (!this.form.username || !this.form.password) {
        this.$toastx({
          title: "请填写完整信息",
          type: "warning",
        });
        return;
      }
      if (!this.form.vaptcha.server || !this.form.vaptcha.token) {
        this.$toastx({
          title: "请先完成人机验证",
          type: "warning",
        });
        return;
      }

      const loading = this.$vs.loading({
        target: this.$refs.loginForm,
        text: "登录中...",
      });

      const original = {
        uname: this.form.username,
        pwd: this.form.password,
        verify: {
          sr: this.form.vaptcha.server,
          tk: this.form.vaptcha.token,
        },
      };

      login(this, original).then((resp) => {
        console.log(resp);

        if (!resp) {
          this.refreshVaptcha();
          loading.close();
        }
      });
    },
    refreshVaptcha() {
      this.$refs.vaptchaVc.resetVerify();
      this.form.vaptcha.server = "";
      this.form.vaptcha.token = "";
    },
  },
};
</script>

