.btn {
  padding: 10px;
}
.loginForm span {
  font-size: 14px;
}
.loginForm {
  display: flex;
  flex-direction: column;
  // padding: 50px;

  .title {
    text-align: center;
    margin-bottom: 20px;
  }
  .subtitle {
    text-align: center;
    font-size: 18px;
    margin-bottom: 20px;
  }
  .regspan {
    margin-top: 40px;
    text-align: center;
  }
  .retrieve {
    text-align: center;
    color: #6e6b7b;
  }
}
.loginForm .btn:first-of-type {
  margin-top: 20px;
  margin-bottom: 0px;
}

@media screen and (min-width: 576px) {
  #loginPage {
    min-width: 450px !important;
  }
}
</style>
